import activityList from "@/template/activityList.vue"
export default {
	components: {
		activityList
	},
	data() {
		return {
			newsList: [],
			cacheTab: [],
			tabIndex: 0,
			screenHeight: 0, //屏幕高度
			isLoading: false, //防止频繁触发
			bottomDistinct: 0, //距离底部多少像素时触发
			pageObj: {
				page: 0,
				pagecount: 0
			},
			noMore: false,
			isScroll: false,
			id: '',
			tabBars: [{
					name: '进行中',
					id: 'jinxingzhong',
				}, {
					name: '未开始',
					id: 'weikai',
				}, {
					name: '已结束',
					id: 'jieshu',
				}
			],
			allData: [
				[],[],[]
			],
			scrollInto: "",
		}
	},
	onLoad() {
		new Promise((resolve, reject) => {
			this.tabBars.forEach((tabBar) => {
				this.newsList.push({
					data: [],
					pageObj: {
						page: 1,
						pagecount: 1
					},
					isLoading: false,
					noMore: true
				});
			});
			resolve()
		}).then(() => {
			this.getAll();
		})
	},
	onPageScroll(e) {
		if (e.scrollTop > 100) {
			this.isScroll = true;
		} else {
			this.isScroll = false;
		}
	},
	methods: {
		getAll() {
			var that = this;
			this.$apiJs.getMyChamberActivity({
			page: this.pageObj.page + 1
			}).then(res => {
				var r_data=res.backlist;
				r_data.forEach((item) => {
					//0、未开始  1、进行中  -1、已结束
					if(item.type==1){
						this.allData[0].push(item)
					}else if(item.type==0){
						this.allData[1].push(item)
					}else{
						this.allData[2].push(item)
					}
				})
			})
		},
		ontabtap(e) {
			let index = e.target.dataset.current || e.currentTarget.dataset.current;
			this.switchTab(index);
		},
		ontabchange(e) {
			let index = e.target.current || e.detail.current;
			this.switchTab(index);
		},
		switchTab(index) {
			var that = this;
			this.tabIndex = index;
			this.scrollInto = this.tabBars[index].id;
		}
	}
}

